/* HEADER STYLES
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.header {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1;
}

.header .nav {
  display: flex;
  align-items: center;
  justify-content: space-between;
}


/* STICKY STYLES
–––––––––––––––––––––––––––––––––––––––––––––––––– */
.header.is-sticky {
  position: fixed;
  box-shadow: 0 5px 16px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  animation: slideDown 0.35s ease-out;
}button

 .header.is-sticky img {
   max-width: 80%;
 }

.header.is-sticky button {
  font-size: 14px;
  padding: 7px 10px;
}
.name_kross{
  text-decoration: none;
  color: black;
}
@keyframes slideDown {
  from {
    transform: translateY(-100%);
  }
  to {
    transform: translateY(0);
  }
}
.reveal{
  transition: 0.5s all ease;
}

.reveal.active{
  transform: translateY(0);
  opacity: 1;
}
